@font-face {
  font-family: "Lexend Deca";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/lexenddeca/v16/K2FifZFYk-dHSE0UPPuwQ7CrD94i-NCKm-U48M1wqxnD.woff2) format("woff2");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/inter/v11/UcCO3FwrK3iLTeHuS_fvQtMwCp50KnMw2boKoduKmMEVuLyfAZ9hiA.woff2) format("woff2");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/inter/v11/UcCO3FwrK3iLTeHuS_fvQtMwCp50KnMw2boKoduKmMEVuFuYAZ9hiA.woff2) format("woff2");
}
html {
  font-size: 0.9375rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #090b1a;
}

.card__content h2 {
  font-family: "Inter";
  font-weight: 700;
}
.card__content p {
  font-family: "Lexend Deca";
  font-weight: 400;
}

.stats__values h3 {
  font-family: "Inter";
  font-weight: 700;
}
.stats__values p {
  font-family: "Lexend Deca";
  font-weight: 400;
}

.attribution {
  color: white;
  font-size: 0.9375rem;
  font-family: "Lexend Deca";
  text-align: center;
  padding: 1.25rem;
}
.attribution a {
  color: #3e52a3;
}

.card {
  display: flex;
  flex-flow: row nowrap;
  padding: 0;
  max-width: 69.375rem;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 71.8125em) {
  .card {
    flex-flow: column-reverse nowrap;
    top: 70%;
    max-width: 23.4375rem;
    transform: translate(-50%, -50%);
  }
  .card .card__content {
    width: 20.625rem;
    padding: 2.1875rem 1.5625rem;
    text-align: center;
    border-radius: 0 0 10px 10px;
  }
  .card .card__content .content__title {
    font-size: 1.75rem;
  }
  .card .card__content .content__desc {
    margin-top: 0.9375rem;
    font-size: 1rem;
    line-height: 1.8;
  }
  .card .card__content .content__stats {
    flex-flow: column nowrap;
    align-items: center;
    text-align: center;
    margin-top: 2.8125rem;
  }
  .card .card__content .content__stats > div:not(:first-child) {
    margin-top: 1.75rem;
  }
  .card .card__hero {
    width: 20.625rem;
  }
  .card .card__hero > img {
    border-radius: 10px 10px 0 0;
  }
}

.card__content {
  margin: 0;
  padding: 4.0625rem;
  padding-right: 6.625rem;
  width: 35.625rem;
  background-color: #1b1938;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.card__content .content__title {
  color: white;
  font-size: 2.1875rem;
  line-height: 1.3;
}
.card__content .content__title > .title__violet {
  color: #aa5cdb;
}
.card__content .content__desc {
  color: rgba(255, 255, 255, 0.75);
  font-size: 0.9rem;
  line-height: 2;
  margin-top: 1.875rem;
}
.card__content .content__stats {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-top: 5.3125rem;
}
.card__content .content__stats .values__header {
  color: white;
  font-size: 1.46875rem;
}
.card__content .content__stats .values__desc {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.1ch;
  padding-top: 0.3125rem;
}

.card__hero {
  position: relative;
  display: block;
  width: 35.625rem;
}
.card__hero::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(164, 55, 231, 0.6);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
@media (max-width: 71.8125em) {
  .card__hero::after {
    border-radius: 10px 10px 0 0;
  }
}
.card__hero > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

html.desktop {
  width: 1440px;
  height: 800px;
}
html.desktop .design {
  display: block;
  width: 1440px;
  height: 800px;
  max-width: unset;
  content: url("../assets/design/desktop-design.jpg");
}
html.mobile {
  width: 375px;
  height: 1530px;
}
html.mobile .design {
  display: block;
  width: 375px;
  height: 1530px;
  max-width: unset;
  content: url("../assets/design/mobile-design.jpg");
}

.design {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 255;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}